<template>
  <div>


    <div class="status_bar">
      <el-menu :router="true" :default-active="name" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="/storage"></el-menu-item>
        <el-menu-item index="/storage/Music">
          
        </el-menu-item>
        <el-menu-item index="/storage/Video">
          
        </el-menu-item>
        <el-menu-item index="/storage/Document">
          
        </el-menu-item>
        <el-menu-item index="/storage/Picture">
          
        </el-menu-item>
        <el-menu-item index="/storage/Other">
          
        </el-menu-item>
        <el-menu-item index="/storage/TrashBin">
          
        </el-menu-item>
        <el-submenu>
          <template slot="title"></template>
          <el-menu-item index="/storage/ShareFolderGroup"></el-menu-item>
          <el-menu-item index="/storage/BtDownload"></el-menu-item>
          <el-menu-item index="/storage/Oss"></el-menu-item>
        </el-submenu>
        <el-menu-item style="float: right;padding-bottom: 0px;">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="el-icon-time"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-plus"></el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus"></el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus-outline"></el-dropdown-item>
              <el-dropdown-item icon="el-icon-check"></el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-check"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
        <el-submenu style="float: right;padding: 0;margin: 0,5px,0,5px" index="User">
          <template slot="title">{{userName}}</template>
          <el-menu-item><i class="el-icon-user"></i><span></span></el-menu-item>
          <el-menu-item index="/storage/admin"><i class="el-icon-setting"></i><span></span></el-menu-item>
          <el-menu-item v-if="isLogin" index="/User/logout"><i class="el-icon-s-flag"></i><span></span>
          </el-menu-item>
        </el-submenu>

      </el-menu>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userName: '',
        rememberme: '',
        name: window.location.pathname,
        today: new Date().toLocaleDateString(),
        items: [],
        isLogin: false
      }
    },
    mounted() {
      this.getCookie();
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      get() {

      },
      getCookie: function() {
        if (document.cookie.length > 0) {
          var arr = document.cookie.split('; '); //
          for (var i = 0; i < arr.length; i++) {
            var arr2 = arr[i].split('='); //
            if (arr2[0] == 'nickName') {
              this.userName = (arr2[1]); //cookieuserName
              this.isLogin = true;
              return;
            }
          }
          this.$router.push("/User/login");
        } else {
          this.$router.push("/User/login");
        }
      }
    }
  }
</script>

<style>
  .status_bar {
    display: inline-block;
    width: 100%;
  }

  .status_bar i {
    margin: 0.3125rem;
  }
</style>



