<template>
  <div v-title :data-title="selfName" style="margin: 0 auto;text-align: center;">
    <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"></video-player>
  </div>

</template>

<script>
  import 'video.js/dist/video-js.css'

  import {
    videoPlayer
  } from 'vue-video-player'

  export default {
    data() {
      return {
        src: '',
        selfName: this.$route.query.file.substring(this.$route.query.file.lastIndexOf("/") + 1),
        parentName: this.$route.query.file.substring(0, this.$route.query.file.lastIndexOf("/") + 1),
        playerOptions: {
          playbackRates: [0.7, 1.0, 1.5, 2.0], // 
          autoplay: false, // true,
          muted: false, // 
          loop: false, // 
          preload: "auto", // <video>auto,
          language: "zh-CN",
          // aspectRatio: "4:3", //  - "16:9""4:3"
          // fluid: true, // trueVideo.js player
          sources: [{
            type: "video/mp4", // git
            src: '' // url
          }],
          poster: '',
          // width: document.documentElement.clientWidth, //
          notSupportedMessage: "", // Video.js
          controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true // 
          }
        },
      }
    },
    mounted() {
      this.get()
    },
    components: {
      videoPlayer
    },
    methods: {
      get() {
        this.$axios.get('/api/disk/getFile', {
          params: {
            pathName: encodeURIComponent(this.parentName),
            fileName: encodeURIComponent(this.selfName)
          }
        }).then(responese => {
          this.playerOptions.sources[0].src = "/api/" + responese.data.result.path;
        })
      },
      dlna() {

      }
    }
  }
</script>

<style>
</style>



