            </td>
          </tr>
        </table>
      </el-card>
    </div>
  </div>
</template>

<script>
  export default {


    // data:{}
    data() {
      //function data(){},es5function
      return{
        //usernamepasswordreturn
        //
        //AA
        //BAB
        //return
        //AB
        user:{
          username:'',
          password:'',
          email:''
          //
        }
      }
    },
    methods:{
      doLogin(){//
        alert(JSON.stringify(this.user))//this.user
      }
    }
  }
</script>

