<template>
  <div>
    <div style="margin-bottom: 50px;">
      <el-table :data="DiskList">
        <el-table-column label="" prop="id" >
        </el-table-column>
        <el-table-column label="" prop="folderName">

        </el-table-column>
        <el-table-column label="" prop="customName">

        </el-table-column>
        <el-table-column label="" prop="deviceName">
            <template slot-scope="scope">
                <el-button size="mini" @click="addDialog=true"></el-button>
            </template>
        </el-table-column>
      </el-table>

      <el-dialog customClass="addDialog" size="full" title="" :visible.sync="addDialog">
          <el-form ref="form" :model="DiskForm">
            <el-form-item label="">
              <el-input v-model="DiskForm.folderName"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="DiskForm.deviceName"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="DiskForm.customName"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="add"></el-button>
              <el-button></el-button>
            </el-form-item>
          </el-form>
        </span>
      </el-dialog>
    </div>
    <!-- <div>
      <el-form ref="form" :model="DiskForm">
        <el-form-item label="">
          <el-input v-model="DiskForm.folderName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="DiskForm.deviceName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="DiskForm.customName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </div> -->
  </div>
</template>

<script>
  export default {
    data() {
      return {
        DiskList: [],
        DiskForm: {
          folderName: '',
          deviceName: '',
          customName: ''
        },
        addDialog:false
      }
    },
    mounted() {
      this.get();
    },
    methods: {
      get() {
        this.$axios.get('/api/v1/admin/hard_Devices').then(responese => {
          this.DiskList = (responese.data.result);
        });

      },
      add() {
        this.$axios.post('/api/v1/admin/insertHardDevices', this.DiskForm).then(responese => {
          console.log(responese.data.result);
        });
      },
      detail() {

      }
    }
  }
</script>

<style>
</style>



